<template lang="pug">

    v-card 
        v-card-title 
            h2 筛选条件

        v-card-text(class="pa-5")
            v-data-table(
                :headers="filter_header"
                :items="filter_data"
                hide-default-footer
                sort-by="field"
            )
                //- 事件
                template(v-slot:item.actions="{item}")
                    v-icon(small class="error--text" @click="deleteItem(item)") mdi-delete 

                template(v-slot:top)
                    v-toolbar(flat class="mb-8")
                        v-row
                            v-col(cols="12" lg="8")
                                v-combobox(
                                    v-model="collection"
                                    :items="collections"
                                    label="选择数据库"
                                )
                        v-spacer

                        v-dialog(v-model="dialog"
                            max-width="700")
                            template(v-slot:activator="{on}")
                                v-btn(color="primary" 
                                    dark 
                                    class="mb-2 text-capitalize"
                                    v-on="on")
                                    v-icon(class="mr-2") mdi-database-plus
                                    span 添加

                            v-card
                                v-card-title 添加筛选条件
                                v-card-text 
                                    v-row(dense)
                                        v-col(cols="12" sm="4")
                                            v-overflow-btn(v-model="selected_field"
                                            :items="filter_fields"
                                            label="筛选字段"
                                            hint="选择筛选字段"
                                            dense)

                                        v-col(cols="12" sm="4")
                                            v-overflow-btn(v-model="add_filter.filter_type"
                                            :items="filter_types"
                                            label="类型"
                                            hint="选择筛选类型"
                                            dense)

                                        v-col(cols="12" sm="4")
                                            v-overflow-btn(v-model="add_filter.value"
                                            :items="filtervalues"
                                            label="值"
                                            hint="选择筛值"
                                            editable
                                            dense)

                                    v-row
                                        v-col(cols="12" sm="0" lg="4")
                                        v-col(cols="12" sm="0" lg="4")
                                        v-col(cols="12" sm="12" lg="4")
                                            v-text-field(
                                                label="编辑值"
                                                v-model="add_filter.value"
                                            )

                                v-card-actions
                                    v-spacer
                                    v-btn(color="error" dark @click="close") 取消
                                    v-btn(color="success" dark @click="save") 确定
                                    

</template>

<script>
const types_map={"":"等于","$lt":"小于","$gt":"大于"};
import { sync,get } from 'vuex-pathify'
export default {
    name: "FilterView",
    data:() => ({
        dialog:"",
        // collections:["井基础数据","油井日报"],
        collection:"",
        fields:[],
        filter_types:[{text:"等于",value:""},{text:"大于",value:"$gt"},{text:"小于",value:"$lt"}],
        filter_values:[],
        selected_field:"",
        selected_type:"",
        selected_value:"",
        


        add_filter:{field:"",filter_type:"",value:""},
        
        filter_data:[    ],
        filter_header:[{text:"选择字段",value:"field"},{text:"筛选类型",value:"filter_type"},{text:"值",value:"value"},{ text: "操作", value: "actions", sortable: false }]
    }),

    methods:{
        deleteItem(item) {
            const index = this.filter_data.indexOf(item);
            confirm("确定删除筛选条件吗？") &&
                this.filter_data.splice(index, 1);

            if (this.filter_data){
                this.$store.dispatch("db/update_query",this.filter_data);
            }
            },

        save() {
            this.add_filter.filter_type=types_map[this.add_filter.filter_type];
            // this.add_filter.value=this.selected_value;
            this.add_filter.field=this.collection_config.field2label[this.add_filter.field];
            this.filter_data.push(this.add_filter); 
            if (this.filter_data){
                this.$store.dispatch("db/update_query",this.filter_data);
            }
            this.close();
            },
        close() {
            this.dialog = false;
            this.add_filter={field:"",filter_type:"",filter_value:""};
            this.selected_field="";
            this.selected_value="";
        },
        // 更新筛选条件


    },
    computed:{
        // 获取用户有权限的数据库
        collections:get('user/collections'),

        collection_config:sync('db/collection_config'),
        all_filter_values:sync('db/all_filter_values'),
        filter_fields:get('db/filter_fields'),
        filtervalues(){
            return this.all_filter_values[this.selected_field]
        }

    },
    watch:{
        // 更新数据库
        collection(){
            this.$store.dispatch("db/update_collection",this.collection.value)
        },
        // 更新待选值
        selected_field(){
            this.add_filter.field=this.selected_field;
            if (this.filtervalues){
                return
            }
            if (this.add_filter.field){
                this.$store.dispatch("db/update_filter_values",{field:this.selected_field,all_filter_values:this.all_filter_values})
            }
        }
    }

}
</script>